.home {
  height: 100vh;
  width: 100vw;
  position: absolute;

  &:after {
    content: "";
    height: 808px;
    width: 808px;
    background: linear-gradient(
      45deg,
      #e6fb04,
      #ff6600,
      #00ff66,
      #00ffff,
      #ff00ff,
      #ff0099,
      #6e0dd0,
      #ff3300,
      #099fff
    );
    filter: blur(5px);
    border-radius: 50%;
    position: absolute;
    bottom: -580px;
    right: 0px;
    z-index: -2;
    animation: homeAnim 5s infinite linear;

    @media screen and (max-width: 650px) {
      height: 400px;
      width: 400px;
      bottom: -270px;
    }

    @keyframes homeAnim {
      0% {
        transform: rotate(0deg);
        filter: blur(5px);
      }
      50% {
        filter: blur(30px);
      }

      100% {
        transform: rotate(360deg);
        filter: blur(5px);
      }
    }

    @media screen and (max-width: 430px) {
      display: none;
    }
  }

  &::before {
    content: "";
    height: 800px;
    width: 800px;
    background: black;
    border-radius: 50%;
    position: absolute;
    bottom: -580px;
    right: 5px;
    z-index: 2;

    @media screen and (max-width: 650px) {
      height: 400px;
      width: 400px;
      bottom: -270px;
    }

    @media screen and (max-width: 430px) {
      display: none;
    }
  }

  .home-main {
    display: grid;
    align-items: center;
    justify-items: center;
    height: 95%;

    .main-content {
      user-select: none;
      margin-bottom: 200px;
      h1 {
        font-size: 3.7rem;
        position: relative;
        font-family: $font-2;

        @media screen and (max-width: 650px) {
          font-size: 2rem;
        }
        @media screen and (min-width: 1200px) {
          font-size: 5rem;
        }

        &:before {
          content: "";
          position: absolute;
          height: 150px;
          width: 150px;
          border-radius: 50%;
          border: 1px rgb(213, 2, 255) solid;
          top: -13px;
          left: -46px;
          z-index: -1;
          animation: h1Anim 1.5s forwards ease;
          transform: scale(0);

          @media screen and (max-width: 850px) {
            height: 110px;
            width: 110px;
            left: -40px;
            top: -15px;
          }

          @keyframes h1Anim {
            to {
              transform: scale(1);
            }
          }
        }
      }
      h2 {
        font-size: 4.2rem;
        font-weight: 100;

        @media screen and (max-width: 650px) {
          font-size: 2rem;
        }
        @media screen and (min-width: 1200px) {
          font-size: 5rem;
        }
      }
    }
  }
}

.logoMarque {
  left: 30px;
  width: 100%;
  height: auto;
}

.logos {
  bottom: 90px;
  display: flex;
  justify-content: space-around;
  padding: 0 70%;
  align-items: center;
  height: 90px;
}

.logoTechnos {
  height: 40px;
  padding: 5px 5px;
}
